.idk{
    font-size: 42px;
    background: -webkit-linear-gradient(70deg, #280037 1.18%, #26bcd3 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
   
}



.bt{
    margin: auto;
    padding: 10px;
    justify-self: center;
    align-self: center;
    width: 150px;
    height: 50px;
    position: absolute;
    top: 100px;
    border-radius:50px ;
    -webkit-border-radius:50px ;
    -moz-border-radius:50px ;
    -ms-border-radius:50px ;
    -o-border-radius:50px ;
    border: none;
    color: rgb(222, 252, 247);
    background-color: rgb(18, 25, 42);
    cursor: pointer;

    box-shadow: inset -5px -5px 10px 3px rgba(0, 0, 0, 0.568);

}



.bt:active{
    box-shadow: inset -1px -1px 8px 3px rgb(103, 99, 99);
    color: rgb(103, 99, 99);
   
}

